<template>
  <view class="house-bindings-page">

    <!-- ÁªëÂÆöËÆ∞ÂΩïÂàóË°® -->
    <view class="bindings-list">
      <view
        v-for="(item, index) in bindingList"
        :key="index"
        class="binding-item"
      >
        <!-- Áä∂ÊÄÅÊ†áÁ≠æ -->
        <view
          v-if="item.status === 1"
          class="status-badge status-pending"
        >
          ÂæÖÂÆ°Ê†∏
        </view>
        <view
          v-else-if="item.status === 2"
          class="status-badge status-approved"
        >
          Â∑≤ÈÄöËøá
        </view>
        <view
          v-else-if="item.status === 3"
          class="status-badge status-rejected"
        >
          Â∑≤ÊãíÁªù
        </view>
        <view
          v-else
          class="status-badge"
        >
          Êú™Áü•
        </view>

        <!-- Âú∞ÂùÄ‰ø°ÊÅØ -->
        <view class="address-info">
          <view class="community-name">
            {{ item.communityName || 'Â∞èÂå∫ÂêçÁß∞' }}
            {{ item.buildingName || 'Ê†ã' }} Ê†ã
            {{ item.roomNumber || 'ÊàøÈó¥' }}
          </view>
        </view>

        <!-- Áî®Êà∑‰ø°ÊÅØ -->
        <view class="user-info">
          <view class="info-row">
            <text class="label">ÂßìÂêçÔºö</text>
            <text class="value">{{ item.realName || '--' }}</text>
          </view>
          <view class="info-row">
            <text class="label">ÂÖ≥Á≥ªÔºö</text>
            <text class="value">{{ getRelationText(item.relationType) }}</text>
          </view>
          <view class="info-row">
            <text class="label">ÁîµËØùÔºö</text>
            <text class="value">{{ item.phone || '--' }}</text>
          </view>
        </view>

        <!-- ÂÆ°Ê†∏‰ø°ÊÅØÔºàÂ¶ÇÊûúÂ∑≤ÂÆ°Ê†∏Ôºâ-->
        <view class="audit-info" v-if="item.status !== 1">
          <view class="info-row">
            <text class="label">ÂÆ°Ê†∏Êó∂Èó¥Ôºö</text>
            <text class="value">{{ formatTime(item.auditTime) }}</text>
          </view>
          <view class="info-row" v-if="item.auditRemark">
            <text class="label">ÂÆ°Ê†∏Â§áÊ≥®Ôºö</text>
            <text class="value">{{ item.auditRemark }}</text>
          </view>
        </view>

        <!-- Êìç‰ΩúÊåâÈíÆ -->
        <view class="action-buttons">
          <button
            v-if="item.status === 2"
            class="btn-unbind"
            @click="unbindHouse(item)"
          >
            Ëß£Èô§ÁªëÂÆö
          </button>
        </view>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view class="empty-state" v-if="bindingList.length === 0 && !loading">
        <text class="empty-icon">üè†</text>
        <text class="empty-text">ÊöÇÊó†ÊàøÂ±ãÁªëÂÆöËÆ∞ÂΩï</text>
        <text class="empty-desc">ÁÇπÂáª‰∏ãÊñπÊåâÈíÆÊ∑ªÂä†ÊàøÂ±ãÁªëÂÆö</text>
      </view>

      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <view class="loading-more" v-if="loading">
        <text>Âä†ËΩΩ‰∏≠...</text>
      </view>
    </view>

    <!-- Â∫ïÈÉ®ÊÇ¨ÊµÆÊ∑ªÂä†ÊåâÈíÆ -->
    <view class="fab-container">
      <button class="fab-button" @click="goToBinding">
        <text class="fab-icon">+</text>
        <text class="fab-text">Ê∑ªÂä†ÁªëÂÆö</text>
      </button>
    </view>
  </view>
</template>

<script>
import {getBinding, houseBindings, houseBindingsDel} from '../../../api/list/house-binding'
export default {
  data() {
    return {
      bindingList: [], // ÁªëÂÆöËÆ∞ÂΩïÂàóË°®
      loading: false,
      hasMore: true
    }
  },

  onLoad() {
    this.loadBindings()
  },

  onPullDownRefresh() {
    this.loadBindings(true)
  },

  onReachBottom() {
    if (this.hasMore && !this.loading) {
      this.loadBindings()
    }
  },

  methods: {
    // Âä†ËΩΩÁªëÂÆöËÆ∞ÂΩï
    async loadBindings(refresh = false) {
      try {
        this.loading = true

        const params = {
          page: refresh ? 1 : Math.ceil(this.bindingList.length / 10) + 1,
          pageSize: 10
        }

        const result = await houseBindings(params)

        // ÈÄöËøáÂÆ°Ê†∏
        // getBinding().then(res=>{  console.log(res) })

        if (result && Array.isArray(result.records)) {
          const newRecords = result.records.map(item => {
            return {
              ...item,
              communityName: item.communityName || 'Â∞èÂå∫ÂêçÁß∞',
              buildingName: item.buildingName || 'Ê•ºÊ†ã',
              roomName: item.roomName || 'ÊàøÈó¥'
            }
          })

          if (refresh) {
            this.bindingList = newRecords
          } else {
            this.bindingList = [...this.bindingList, ...newRecords]
          }

          this.hasMore = newRecords.length >= params.pageSize
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÁªëÂÆöËÆ∞ÂΩïÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'Ëé∑ÂèñÁªëÂÆöËÆ∞ÂΩïÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.loading = false
        if (refresh) {
          uni.stopPullDownRefresh()
        }
      }
    },


    // Ëé∑ÂèñÂÖ≥Á≥ªÊñáÊú¨
    getRelationText(relationType) {
      const relationMap = {
        1: '‰∏ö‰∏ª',
        2: 'ÂÆ∂‰∫∫',
        3: 'ÁßüÂÆ¢',
        4: 'ÂÖ∂‰ªñ'
      }
      return relationMap[relationType] || 'Êú™Áü•'
    },

    // Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(timeStr) {
      if (!timeStr) return '--'
      const date = new Date(timeStr)
      return date.toLocaleString('zh-CN')
    },



    // Ëß£Èô§ÁªëÂÆö
    async unbindHouse(item) {
      try {
        const result = await uni.showModal({
          title: 'Á°ÆËÆ§Ëß£Áªë',
          content: 'Á°ÆÂÆöË¶ÅËß£Èô§ËØ•ÊàøÂ±ãÁªëÂÆöÂêóÔºü',
          confirmText: 'Á°ÆÂÆö',
          cancelText: 'ÂèñÊ∂à'
        })

        if (result.confirm) {
          uni.showLoading({ title: 'Â§ÑÁêÜ‰∏≠...' })
          // ÂêåÊ†∑‰ΩøÁî® houseBindingsDel Êé•Âè£ËøõË°åËß£Áªë
          await houseBindingsDel(item.id)
          uni.hideLoading()
          uni.showToast({
            title: 'Ëß£ÁªëÊàêÂäü',
            icon: 'success'
          })
          this.loadBindings(true)
        }
      } catch (error) {
        uni.hideLoading()
        console.error('Ëß£Èô§ÁªëÂÆöÂ§±Ë¥•:', error)
        uni.showToast({
          title: error.message || 'Ëß£ÁªëÂ§±Ë¥•',
          icon: 'none'
        })
      }
    },


    // ÂéªÁªëÂÆöÈ°µÈù¢
    goToBinding() {
      uni.navigateTo({
        url: '/subpackages/user/house-binding/house-binding'
      })
    }
  }
}
</script>

